*{
    margin:0;
    padding:0;
}

.container {
    display: grid;
    grid-template-columns: repeat(3, minmax(100px, 1fr)); /* Колонки займут доступное пространство */
    column-gap: 17px;
    row-gap:  10px;
    justify-content: center; /* Горизонтальное выравнивание */
    align-items: center; /* Вертикальное выравнивание */
}
.usercard{
    color:white;
    height: 20px;
    weight: 20px;
    font-weight: 10px;
    font-family: Helvetica;
}

p {
    color:white;
    height: 20px;
    weight: 20px;
    font-weight: 10px;
    font-family: Helvetica;
    bottom: 20px;
}
